import styles from './../Users.module.css';
import React from 'react';
import Avatar from './../../Shared/Avatar.jsx';
import {NavLink} from 'react-router-dom';

const User = (props) => {
  return (
    <div className={styles.user}>
      <div className={styles.block__left}>
        <NavLink to={props.userURL}>
          <Avatar src={props.avatarSrc} name={props.name} />
        </NavLink>
        <span className={styles.name}>{props.name}</span>
        {props.followed ?
          <button  onClick={() => {props.onUnfollow(props.id)}} disabled={props.usersInFollowingProgress.some(user2 => user2 === props.id ? true : false )} className={styles.follow}>unfollow</button> :
          <button  onClick={() => {props.onFollow(props.id)}} disabled={props.usersInFollowingProgress.some(user2 => user2 === props.id ? true : false )} className={styles.follow}>follow</button>
        }
      </div>
      <div className={styles.block__right}>
        <span className={styles.type}>I was born as </span>
        <span className={styles.location}>I currently living at
          <span className={styles.city}>{props.city} </span>
          <span className={styles.country}>{props.country}</span>
        </span>
        <div className={styles.status}>Status: {props.status}</div>
      </div>
    </div>
  )
}

export default User;
