import React from 'react';
import {setCurrentPage, setToggleIsFetching, getUsers,
        onFollow, onUnfollow} from '../../redux/users-reducer.ts';
import {connect} from 'react-redux';
import Users from './Users.jsx';
import withAuthRedirect from './../../hoc/withAuthRedirect.js';
import {compose} from 'redux';

class UsersAPI extends React.Component {
    onPageChange = (e) => {
      this.props.setCurrentPage(e);
      this.props.getUsers(e, this.props.usersPerPage);
    }

    render = () => {
        return (
            <Users
                users = {this.props.users}
                onPageChange = {this.onPageChange}
                usersCount = {this.props.usersCount}
                usersPerPage = {this.props.usersPerPage}
                currentPage = {this.props.currentPage}
                isFetching = {this.props.isFetching}
                usersInFollowingProgress = {this.props.usersInFollowingProgress}
                onFollow = {this.props.onFollow}
                onUnfollow = {this.props.onUnfollow}
            />
        )
    }
    componentDidMount() {
      this.props.getUsers(this.props.currentPage, this.props.usersPerPage);
    }
}

let mapStateToProps = (state) => {
  return {
    users: state.usersData.users,
    usersCount: state.usersData.usersCount,
    usersPerPage: state.usersData.usersPerPage,
    currentPage: state.usersData.currentPage,
    isFetching: state.usersData.isFetching,
    usersInFollowingProgress: state.usersData.usersInFollowingProgress
  }
}

export default compose(
  connect(mapStateToProps, {
          setCurrentPage,
          setToggleIsFetching,
          getUsers,
          onFollow,
          onUnfollow
      }
  ),
  withAuthRedirect
)(UsersAPI);
